<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process241"></a>

<p><br></p>
<p><span class="rvts17">侵略者（</span><span class="rvts50">Invaders</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:189,Y:847</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：黑环士兵（</span><span class="rvts20">The Black Ring Member</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">支持黑环势力抵御魔导师，或支持魔导师抵御黑环势力。</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1354.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在无名岛上，有好几处黑环势力据点。尝试使用较为和蔼的语气（说你是孤狼，不参与魔导师与黑环的战争；或者假装你是阿尔米亚的信徒）通过语言环境测试，并帮助他们的首领瘦黄男人杀掉亚历山大。</span>
<br><span class="rvts21">或者，也可以去精灵营地选择帮助亚历山大抵御黑环势力。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



